.result {
    .inner {max-width: 800px; margin: 0 auto; height: 100%;}
    .resultInner {padding: 5rem 0 0; position: relative; z-index: 2;}
    .result__cnts {
        display: flex; gap: 2rem; margin: 2rem 0 0;
    }
    .summary {
        display: flex; flex-direction: column; gap: 1rem; flex: 1;
        div {
            display: flex; justify-content: space-between; background: #fff; height: 3rem; align-items: center; padding: 0 0.5rem; border-radius: 10px;
            border: 2px solid #2fc867;
            & + div {border-color: #f27b7b;}
        }
    }
    .quiz__table .btnWrap p {height: 3rem;}
    .quiz__table td span {display: flex; align-items: center; justify-content: center; height: 100%; background-position: center !important; background-size: contain !important;}
    p.selected {background: #2fc867; color: #fff;}
    p.value {background: $pirmaryColor; color: #fff;}
}

@media screen and (max-width: 768px) {
  .result {
    .result__cnts {flex-wrap: wrap;}
    .summary {flex: auto; width: 100%; max-width: 30rem; margin: 0 auto;}
    .quiz__right {width: 100%; max-width: 30rem; margin: 0 auto;}
    .btnSubmit {width: 100%;}
  }

}




$particles: 50;
$width: 500;
$height: 500;

// Create the explosion...
$box-shadow: ();
$box-shadow2: ();
@for $i from 0 through $particles {
  $box-shadow: $box-shadow,
               random($width)-$width / 2 + px
               random($height)-$height / 1.2 + px
               hsl(random(360), 100, 50);
  $box-shadow2: $box-shadow2, 0 0 #fff
}
@mixin keyframes ($animationName) {
    @-webkit-keyframes #{$animationName} {
        @content;
    }

    @-moz-keyframes #{$animationName} {
        @content;
    }

    @-o-keyframes #{$animationName} {
        @content;
    }

    @-ms-keyframes #{$animationName} {
        @content;
    }

    @keyframes #{$animationName} {
        @content;
    }
}

@mixin animation-delay ($settings) {
    -moz-animation-delay: $settings;
    -webkit-animation-delay: $settings;
    -o-animation-delay: $settings;
    -ms-animation-delay: $settings;
    animation-delay: $settings;
}

@mixin animation-duration ($settings) {
    -moz-animation-duration: $settings;
    -webkit-animation-duration: $settings;
    -o-animation-duration: $settings;
    -ms-animation-duration: $settings;
    animation-duration: $settings;
}

@mixin animation ($settings) {
    -moz-animation: $settings;
    -webkit-animation: $settings;
    -o-animation: $settings;
    -ms-animation: $settings;
    animation: $settings;
}

@mixin transform ($settings) {
    transform: $settings;
    -moz-transform: $settings;
    -webkit-transform: $settings;
    -o-transform: $settings;
    -ms-transform: $settings;
}


.pyro {position: absolute; left: 0; top: 0;width: 100%; height: 100%; overflow: hidden;}
.pyro > .before, .pyro > .after {
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  box-shadow: $box-shadow2;
  @include animation((1s bang ease-out infinite backwards, 1s gravity ease-in infinite backwards, 5s position linear infinite backwards));
}
    
.pyro > .after {
  @include animation-delay((1.25s, 1.25s, 1.25s));
  @include animation-duration((1.25s, 1.25s, 6.25s));
}
        
@include keyframes(bang) {
  to {
    box-shadow:$box-shadow;
  }
}
    
@include keyframes(gravity)  {
  to {
    @include transform(translateY(200px));
    opacity: 0;
  }
}
    
@include keyframes(position) {
  0%, 19.9% {
    margin-top: 10%;
    margin-left: 40%;
  }
  20%, 39.9% {
    margin-top: 40%;
    margin-left: 30%;
  }
  40%, 59.9% {  
    margin-top: 20%;
    margin-left: 70%
  }
  60%, 79.9% {  
    margin-top: 30%;
    margin-left: 20%;
  }
  80%, 99.9% {  
    margin-top: 30%;
    margin-left: 80%;
  }
}
